.header-nav__nav-toggle {
  position: absolute;
  top: 12px;
  right: 5px;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
  outline: none;
}
.header-nav__nav-toggle:hover,
.header-nav__nav-toggle:active {
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header-nav__nav-toggle {
    display: none;
  }
}
.header-nav__nav-toggle::before {
  content: "";
  position: absolute;
  top: 17px;
  left: 30px;
  width: 21px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
}
.header-nav__nav-toggle:active::before {
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
}
.main-nav--opened .header-nav__nav-toggle {
  top: 12px;
  right: 5px;
}
.main-nav--opened .header-nav__nav-toggle::before,
.main-nav--opened .header-nav__nav-toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 21px;
  height: 2px;
  background-color: #231f20;
}
.main-nav--opened .header-nav__nav-toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .header-nav__nav-toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .header-nav__nav-toggle:active::before,
.main-nav--opened .header-nav__nav-toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
